import * as React from 'react';
import Registro from '../registro/Registro.js';
import Cuestionario from '../cuestionario/Cuestionario.js';

export function Entrevista() {
    const [completeStep, setCompleteStep] = React.useState({
        registro: false,
        cuestionario: false,
        agendaCita: false
    });

    function ShowStep() {
        if (!completeStep.registro) {
            return <Registro CompleteStep={completeStep} onFinishStep={setCompleteStep} />;
        }
        if (!completeStep.cuestionario) {
            return <Cuestionario CompleteStep={completeStep} onFinishStep={setCompleteStep} />;
        }
    }

    return <ShowStep />;
}