import * as React from 'react';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import { Entrevista } from './entrevista/Entrevista';
import PortadaInfo from './portada/Portada';

//"homepage": "https://leotorres9286.github.io/interviews-app/"

const theme = createTheme({
  palette: {
    primary: {
      light: '#8559da',
      main: '#512da8',
      dark: '#140078',
      contrastText: '#fff',
    },
    secondary: {
      light: '#58a5f0',
      main: '#0277bd',
      dark: '#004c8c',
      contrastText: '#fff',
    },
  },
});

function App() {
  return (
    <ThemeProvider theme={theme}>
      <Router>
        <Switch>
          <Route path="/entrevista">
            <Entrevista />
          </Route>
          <Route path="/">
            <PortadaInfo />
          </Route>
        </Switch>
      </Router>
    </ThemeProvider>
  );
}

export default App;
