function FormRegistro({
    candidateData,
    onCandidateData,
    inputRequired,
    onInputRequiredCh,
    validateSubmit,
    onValidateForm,
    validateForm,
    loading }) {

    const handleName = (e) => {
        const dataName = e.target.value;
        const reValid = new RegExp('^[A-Z ]*$', 'i');
        if (!reValid.test(dataName)) {
            onValidateForm({ ...validateForm, name: { error: true, help: 'Error (letras y espacios).' } });
        } else {
            onValidateForm({ ...validateForm, name: { error: false, help: '' } });
        }
        onCandidateData({
            ...candidateData,
            name: dataName
        });
        if (
            dataName !== ''
            && candidateData.lastname !== ''
            && candidateData.phone !== ''
            && candidateData.email !== ''
            && candidateData.postalcode !== ''
            && candidateData.gender !== ''
            && candidateData.birthdate !== null
        ) {
            onInputRequiredCh(false);
        } else {
            onInputRequiredCh(true);
        }
    };

    const handleLastName = (e) => {
        const dataLastName = e.target.value;
        const reValid = new RegExp('^[A-Z ]*$', 'i');
        if (!reValid.test(dataLastName)) {
            onValidateForm({ ...validateForm, lastname: { error: true, help: 'Error (letras y espacios).' } });
        } else {
            onValidateForm({ ...validateForm, lastname: { error: false, help: '' } });
        }
        onCandidateData({
            ...candidateData,
            lastname: dataLastName
        });
        if (
            dataLastName !== ''
            && candidateData.name !== ''
            && candidateData.phone !== ''
            && candidateData.email !== ''
            && candidateData.postalcode !== ''
            && candidateData.gender !== ''
            && candidateData.birthdate !== null
        ) {
            onInputRequiredCh(false);
        } else {
            onInputRequiredCh(true);
        }
    };

    const handlePhone = (e) => {
        const dataPhone = e.target.value;
        const reValid = new RegExp('^[67]{1}[0-9]{8}$');
        if (!reValid.test(dataPhone)) {
            onValidateForm({ ...validateForm, phone: { error: true, help: 'Error (9 dgitos comenzando por 6-7).' } });
        } else {
            onValidateForm({ ...validateForm, phone: { error: false, help: '' } });
        }
        onCandidateData({
            ...candidateData,
            phone: dataPhone
        });
        if (
            dataPhone !== ''
            && candidateData.name !== ''
            && candidateData.lastname !== ''
            && candidateData.email !== ''
            && candidateData.postalcode !== ''
            && candidateData.gender !== ''
            && candidateData.birthdate !== null
        ) {
            onInputRequiredCh(false);
        } else {
            onInputRequiredCh(true);
        }
    };

    const handleEmail = (e) => {
        const dataEmail = e.target.value;
        const reValid = new RegExp("^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$");
        if (!reValid.test(dataEmail)) {
            onValidateForm({ ...validateForm, email: { error: true, help: 'Error, correo no vlido.' } });
        } else {
            onValidateForm({ ...validateForm, email: { error: false, help: '' } });
        }
        onCandidateData({
            ...candidateData,
            email: dataEmail
        });
        if (
            dataEmail !== ''
            && candidateData.name !== ''
            && candidateData.lastname !== ''
            && candidateData.phone !== ''
            && candidateData.postalcode !== ''
            && candidateData.gender !== ''
            && candidateData.birthdate !== null
        ) {
            onInputRequiredCh(false);
        } else {
            onInputRequiredCh(true);
        }
    };

    const handlePostalCode = (e) => {
        const dataPostalCode = e.target.value;
        const reValid = new RegExp("^[0-9]{5}$");
        if (!reValid.test(dataPostalCode)) {
            onValidateForm({ ...validateForm, postalcode: { error: true, help: 'Error (cdigo de 5 dgitos).' } });
        } else {
            onValidateForm({ ...validateForm, postalcode: { error: false, help: '' } });
        }
        onCandidateData({
            ...candidateData,
            postalcode: dataPostalCode
        });
        if (
            dataPostalCode !== ''
            && candidateData.name !== ''
            && candidateData.lastname !== ''
            && candidateData.phone !== ''
            && candidateData.email !== ''
            && candidateData.gender !== ''
            && candidateData.birthdate !== null
        ) {
            onInputRequiredCh(false);
        } else {
            onInputRequiredCh(true);
        }
    };

    const handleGender = (e) => {
        const dataGender = e.target.value;
        onCandidateData({
            ...candidateData,
            gender: dataGender
        });
        if (
            dataGender !== ''
            && candidateData.name !== ''
            && candidateData.lastname !== ''
            && candidateData.phone !== ''
            && candidateData.email !== ''
            && candidateData.postalcode !== ''
            && candidateData.birthdate !== null
        ) {
            onInputRequiredCh(false);
        } else {
            onInputRequiredCh(true);
        }
    };

    const handleBirthDate = (e) => {
        const dataBirthDate = e;
        onCandidateData({
            ...candidateData,
            birthdate: dataBirthDate
        });
        if (
            dataBirthDate !== null
            && candidateData.name !== ''
            && candidateData.lastname !== ''
            && candidateData.phone !== ''
            && candidateData.email !== ''
            && candidateData.postalcode !== ''
            && candidateData.gender !== ''
        ) {
            onInputRequiredCh(false);
        } else {
            onInputRequiredCh(true);
        }
    };

    return (
        <Box>
            <Box>
                <Grid container spacing={4} rowSpacing={2}>
                    <Grid item xs={12} sm={5}>
                        <TextField
                            id="input-name"
                            label="Nombre"
                            variant="standard"
                            required
                            fullWidth
                            error={validateSubmit.name.error}
                            helperText={validateSubmit.name.help}
                            disabled={loading}
                            onChange={handleName}
                            value={candidateData.name}
                            inputProps={{
                                inputMode: 'text',
                                pattern: '^[A-Za-z ]*$'
                            }}
                        />
                    </Grid>
                    <Grid item xs={12} sm={7}>
                        <TextField
                            id="input-lastname"
                            label="Apellidos"
                            variant="standard"
                            required
                            fullWidth
                            error={validateSubmit.lastname.error}
                            helperText={validateSubmit.lastname.help}
                            disabled={loading}
                            onChange={handleLastName}
                            value={candidateData.lastname}
                            inputProps={{
                                inputMode: 'text',
                                pattern: '^[A-Za-z ]*$'
                            }}
                        />
                    </Grid>
                    <Grid item xs={12} sm={4}>
                        <TextField
                            id="input-phone"
                            placeholder='123456789'
                            label="Tel&eacute;fono"
                            variant="standard"
                            type="tel"
                            required
                            fullWidth
                            error={validateSubmit.phone.error}
                            helperText={validateSubmit.phone.help}
                            disabled={loading}
                            onChange={handlePhone}
                            value={candidateData.phone}
                            inputProps={{
                                inputMode: 'tel',
                                pattern: '^[67]{1}[0-9]{8}$'
                            }}
                        />
                    </Grid>
                    <Grid item xs={12} sm={8}>
                        <TextField
                            id="input-email"
                            label="Correo"
                            variant="standard"
                            type="email"
                            required
                            fullWidth
                            error={validateSubmit.email.error}
                            helperText={validateSubmit.email.help}
                            disabled={loading}
                            onChange={handleEmail}
                            value={candidateData.email}
                            inputProps={{
                                pattern: "^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$"
                            }}
                        />
                    </Grid>
                    <Grid item xs={12} sm={4}>
                        <TextField
                            id="input-postalcode"
                            label="C&oacute;digo Postal"
                            placeholder='12345'
                            variant="standard"
                            type="number"
                            required
                            fullWidth
                            error={validateSubmit.postalcode.error}
                            helperText={validateSubmit.postalcode.help}
                            disabled={loading}
                            onChange={handlePostalCode}
                            value={candidateData.postalcode}
                            inputProps={{
                                inputMode: 'numeric',
                                pattern: '^[0-9]{5}$'
                            }}
                        />
                    </Grid>
                    <Grid item xs={12} sm={4}>
                        <FormControl fullWidth required variant="standard" disabled={loading}>
                            <InputLabel id="demo-simple-select-label">G&eacute;nero</InputLabel>
                            <Select
                                value={candidateData.gender}
                                onChange={handleGender}
                                inputProps={{
                                    name: 'gender',
                                    id: 'input-gender"',
                                }}
                            >
                                <MenuItem key='gender-hombre' value='h'>Hombre</MenuItem>
                                <MenuItem key='gender-mujer' value='m'>Mujer</MenuItem>
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={12} sm={4}>
                        <LocalizationProvider dateAdapter={AdapterDateFns} locale={es}>
                            <DatePicker
                                label="Fecha Nacimiento"
                                value={candidateData.birthdate}
                                onChange={handleBirthDate}
                                openTo='year'
                                disableHighlightToday
                                defaultCalendarMonth={subMonths(new Date(), 214)}
                                maxDate={subMonths(new Date(), 212)}
                                minDate={subYears(new Date(), 55)}
                                disabled={loading}
                                renderInput={(params) => <TextField
                                    {...params}
                                    required
                                    fullWidth
                                    variant="standard"
                                    id="input-birthdate"
                                />}
                            />
                        </LocalizationProvider>
                    </Grid>
                </Grid>
            </Box>
            <Box
                sx={{
                    mt: '5vh'
                }}
            >
                <Typography
                    variant='subtitle2'
                >
                    Le informamos que los datos personales que nos proporcione rellenando el presente formulario sern tratados por la empresa como responsable. Estos datos podrn ser usados nicamente para comunicaciones relacionadas a la candidatura de una oferta de empleo. Al presionar la opci&oacute;n de (ACEPTO), usted otorga su consentimiento expreso para la recogida y el tratamiento de sus datos personales exclusivamente con este fin de ofrecerle un empleo. Posterior a la candidatura sern TOTALMENTE eliminados.
                </Typography>
                <Typography
                    variant='body1'
                    align='center'
                    sx={{
                        mt: 2
                    }}
                >
                    &#191;Acepta nuestra pol&iacute;tica de privacidad?
                </Typography>
            </Box>
        </Box>
    );
}

const iniCandidateData = {
    name: '',
    lastname: '',
    phone: '',
    email: '',
    postalcode: '',
    gender: '',
    birthdate: null,
};

const iniValidateForm = {
    name: { error: false, help: '' },
    lastname: { error: false, help: '' },
    phone: { error: false, help: '' },
    email: { error: false, help: '' },
    postalcode: { error: false, help: '' },
    gender: { error: false, help: '' },
    birthdate: { error: false, help: '' }
};

export default function Registro({ CompleteStep, onFinishStep }) {
    const [activeStep, setActiveStep] = React.useState(0);
    const [candidateData, setCandidateData] = React.useState(iniCandidateData);
    const [inputRequired, setInputRequired] = React.useState(true);
    const [loading, setLoading] = React.useState(false);
    const [endRegistro, setEndRegistro] = React.useState(0);
    const [labelButtonEnd, setLabelButtonEnd] = React.useState('Guardar');
    const [validateForm, setValidateForm] = React.useState(iniValidateForm);
    const [validateSubmit, setValidateSubmit] = React.useState(iniValidateForm);

    const handleNext = () => {
        setActiveStep((prevActiveStep) => prevActiveStep + 1);
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    const handleEndCancel = () => {
        setActiveStep(0);
        setCandidateData(iniCandidateData);
        setInputRequired(false);
        setLoading(false);
        setEndRegistro(0);
        setLabelButtonEnd('Guardar');
        setValidateSubmit(iniValidateForm);
        setValidateForm(iniValidateForm);
    };

    const RegistroSuccess = () => {
        return (
            <Box>
                <Typography
                    variant="h5"
                    align='center'
                    sx={{
                        mt: 2,
                        mb: 1
                    }}>
                    HA TERMINADO EL REGISTRO
                </Typography>
                <Typography
                    variant="subtitle1"
                    align='center'
                    sx={{
                        mt: 2,
                        mb: 1
                    }}>
                    GRACIAS<br /> &#191;Podemos comenzar el cuestionario?
                </Typography>
            </Box>
        );
    };

    const RegistroFail = ({ failState }) => {
        const textFail = () => {
            switch (failState) {
                case 2:
                    return 'Su participacin en la convocatoria a esta candidatura HA EXPIRADO.';

                case 3:
                    return 'Hemos alcanzado el lmite de candidatos registrados para esta candidatura.';

                default:
                    return 'Usted no ha sido convocado para participar en esta candidatura.';
            }
        }

        return (
            <Box>
                <Typography
                    variant="h5"
                    align='center'
                    sx={{
                        mt: 2,
                        mb: 1
                    }}>{textFail()}</Typography>
                <Typography
                    variant="subtitle1"
                    align='center'
                    sx={{
                        mt: 2,
                        mb: 1
                    }}>
                    Gracias por su tiempo.
                    <br /> &#191;Desea que guardemos sus datos y en otra ocaci&oacute;n contactemos con usted?
                </Typography>
            </Box>
        );
    };

    const handleSubmitRegForm = () => {
        let flagErrorValidate = false;
        const checkValid = () => {
            Object.entries(validateForm).forEach(([key, input]) => {
                if (!flagErrorValidate && validateForm[key].error) {
                    flagErrorValidate = true;
                }
            });
            return Promise.resolve();
        };
        checkValid().then(() => {
            if (!flagErrorValidate) {
                const fakeNumbers = [
                    { phone: '612345678' }
                ];
                setEndRegistro(0);
                setLoading(true);
                setTimeout(() => {
                    Promise.all(fakeNumbers.map((number) => {
                        if (number.phone === candidateData.phone) {
                            setEndRegistro(1);
                            setLabelButtonEnd("Comenzar");
                        }
                    })).then(() => {
                        setLoading(false);
                        handleNext();
                    });
                }, 1000);
            } else {
                setValidateSubmit({ ...validateForm });
            }
        });
    };

    const handleConfirm = () => {
        setLoading(true);
        setTimeout(() => {
            alert(JSON.stringify(candidateData));
            onFinishStep({ ...CompleteStep, registro: true });
        }, 1000);
    };

    return (
        <Box
            sx={{
                width: { sm: '90%', md: '60%' },
                height: '100%',
                px: '10%',
                pt: '5%',
                pb: '5%',
                my: { sm: 0, md: '5vh' },
                mx: { sm: 0, md: '10%' },
                boxShadow: { sm: 0, md: 1 },
            }}
        >
            <Box sx={{ width: '100%', mt: '2vh' }}>
                <Box sx={{ mx: { sm: '15%', xs: 0 } }}>
                    <Stepper activeStep={activeStep}>
                        {steps.map((label, index) => {
                            const stepProps = {};
                            const labelProps = {};
                            return (
                                <Step key={label} {...stepProps}>
                                    <StepLabel {...labelProps}>{label}</StepLabel>
                                </Step>
                            );
                        })}
                    </Stepper>
                </Box>
                <Box sx={{ pt: 5 }} />
                {activeStep === steps.length ? (
                    <Box>
                        {endRegistro === 1 ? (
                            <RegistroSuccess />
                        ) : (
                            <RegistroFail failState={endRegistro} />
                        )}
                        <Box sx={{ display: 'flex', flexDirection: 'row', py: '10vh' }}>
                            {endRegistro === 1 ? (
                                <Button
                                    variant='contained'
                                    color="inherit"
                                    disabled={activeStep === 0}
                                    onClick={handleEndCancel}
                                    disabled={loading}
                                    sx={{ borderRadius: 0 }}
                                >Cancelar</Button>
                            ) : (
                                <Link to={'/'} style={{
                                    textDecoration: 'none',
                                    color: 'inherit'
                                }}>
                                    <Button
                                        variant='contained'
                                        color="inherit"
                                        disabled={activeStep === 0}
                                        onClick={handleEndCancel}
                                        disabled={loading}
                                        sx={{ borderRadius: 0 }}
                                    >Cancelar</Button>
                                </Link>
                            )}
                            <Box sx={{ flex: '1 1 auto' }} />
                            <LoadingButton
                                onClick={handleConfirm}
                                loading={loading}
                                variant="contained"
                                color='success'
                                sx={{ borderRadius: 0 }}
                            >{labelButtonEnd}</LoadingButton>
                        </Box>
                    </Box>

                ) : (
                    <React.Fragment>
                        {(activeStep === 0) &&
                            <InfoInicial />
                        }
                        {(activeStep === 1) &&
                            <FormRegistro
                                candidateData={candidateData}
                                onCandidateData={setCandidateData}
                                inputRequired={inputRequired}
                                onInputRequiredCh={setInputRequired}
                                validateSubmit={validateSubmit}
                                onValidateForm={setValidateForm}
                                validateForm={validateForm}
                                loading={loading}
                            />
                        }
                        <Box sx={{ display: 'flex', flexDirection: 'row', py: '10vh' }}>
                            {activeStep === 0 ? (
                                <Link
                                    to={'/'}
                                    style={{
                                        textDecoration: 'none',
                                        color: 'inherit'
                                    }}
                                >
                                    <Button
                                        variant='contained'
                                        color="inherit"
                                        disabled={loading}
                                        sx={{ borderRadius: 0 }}
                                    >Regresar</Button>
                                </Link>
                            ) : (
                                <Button
                                    variant='contained'
                                    color="inherit"
                                    onClick={handleBack}
                                    disabled={loading}
                                    sx={{ borderRadius: 0 }}
                                >Regresar</Button>
                            )}
                            <Box sx={{ flex: '1 1 auto' }} />
                            {activeStep === steps.length - 1 ? (
                                <LoadingButton
                                    onClick={handleSubmitRegForm}
                                    loading={loading}
                                    disabled={inputRequired}
                                    variant="contained"
                                    sx={{ borderRadius: 0 }}
                                >Acepto</LoadingButton>
                            ) : (
                                <Button
                                    variant='contained'
                                    onClick={handleNext}
                                    sx={{ borderRadius: 0 }}
                                >Siguiente</Button>
                            )}
                        </Box>
                    </React.Fragment>
                )}
            </Box>
        </Box>
    );
}