import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import { Checkbox, Divider, FormControlLabel, FormGroup, LinearProgress, List, ListItem, ListItemIcon, ListItemText, Radio, RadioGroup, Stack, TextField, Typography } from '@mui/material';
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';

function QuestionDisponibleLaboral({ CuestData, onCuestDataChange }) {
    const handleAswer = (resp) => {
        onCuestDataChange({
            ...CuestData,
            lineProgress: 20,
            disponibleIdeal: resp.target.value === 's'
        });
    };

    return (
        <Box sx={{ px: { sm: 0, md: '20%' }, pt: '10vh' }}>
            <Typography
                variant='h5'
                align='center'
            >&#191;Tienes disponibilidad inmediata en el horario de 9H a 15H?</Typography>
            <Box sx={{ px: '10%', my: '10vh' }} textAlign='center'>
                <Button sx={{ mr: '10%' }} variant='outlined' onClick={handleAswer} value='n'>No</Button>
                <Button sx={{ ml: '10%' }} variant='outlined' onClick={handleAswer} value='s'>Si</Button>
            </Box>
        </Box>
    );
}

function QuestionAtencionCliente({ CuestData, onCuestDataChange }) {
    const handleAswer = (resp) => {
        onCuestDataChange({
            ...CuestData,
            lineProgress: 30,
            likeClientHelp: resp.target.value === 's'
        });
    };

    return (
        <Box>
            <Typography
                variant='h5'
                align='center'
                sx={{ mt: '10vh' }}
            >&#191;Te gusta la Atenci&oacute;n al Cliente?</Typography>
            <Box sx={{ my: '10vh' }} textAlign='center'>
                <Button sx={{ mr: '10%' }} variant='outlined' onClick={handleAswer} value='n'>No</Button>
                <Button sx={{ ml: '10%' }} variant='outlined' onClick={handleAswer} value='s'>Si</Button>
            </Box>
        </Box>
    );
}

function QuestionTiempo({ CuestData, onCuestDataChange }) {
    const [responseQ, setResponseQ] = React.useState({
        situation: '',
        timeStart: '',
        timeEnd: ''
    });
    const [error, setError] = React.useState(true);

    const handleSubmit = (resp) => {
        resp.preventDefault();

        onCuestDataChange({
            ...CuestData,
            lineProgress: 50,
            timeWork: responseQ
        });
    };

    const handleRadioSituation = (e) => {
        const valueS = e.target.value;
        setResponseQ({ ...responseQ, situation: valueS });
        if (valueS !== '' && responseQ.timeStart !== '' && responseQ.timeEnd !== '') {
            setError(false);
        }
    };

    const handleInputTimeStart = (e) => {
        const valueS = e.target.value;
        setResponseQ({ ...responseQ, timeStart: valueS });
        if (valueS !== '' && responseQ.situation !== '' && responseQ.timeEnd !== '') {
            setError(false);
        }
    };
    const handleInputTimeEnd = (e) => {
        const valueS = e.target.value;
        setResponseQ({ ...responseQ, timeEnd: valueS });
        if (valueS !== '' && responseQ.timeStart !== '' && responseQ.situation !== '') {
            setError(false);
        }
    };

    const handleButtonBack = (e) => {
        onCuestDataChange({
            ...CuestData,
            lineProgress: 20
        });
    };

    return (
        <Box
            component='form'
            onSubmit={handleSubmit}
        >
            <Typography
                variant='h5'
                align='center'
                sx={{ mt: '10vh' }}
            >&#191;Est&aacute;s estudiando o trabajando actualmente?</Typography>
            <Box
                display="flex"
                alignItems="center"
                justifyContent="center"
                sx={{ mt: '2vh' }}
            >
                <RadioGroup
                    id='radio-dedicaion'
                    aria-label="dedicacion"
                    name="radio-dedicacion"
                    value={responseQ.situation}
                    onChange={handleRadioSituation}
                    row
                >
                    <FormControlLabel value="estudiante" control={<Radio />} label="Estudio" />
                    <FormControlLabel value="trabajador" control={<Radio />} label="Trabajo" />
                </RadioGroup>
            </Box>
            <Typography
                variant='h5'
                align='center'
                sx={{ mt: '7vh' }}
            >&#191;En qu&eacute; horario?</Typography>
            <Box
                display="flex"
                alignItems="center"
                justifyContent="center"
                sx={{ mt: '2vh' }}
            >
                <TextField
                    id="input-time-start"
                    label="Desde (H)"
                    variant="standard"
                    type="number"
                    value={responseQ.timeStart}
                    onChange={handleInputTimeStart}
                    required
                    inputProps={{
                        inputMode: 'numeric',
                        pattern: '[0-9]*',
                        min: 0,
                        max: 24
                    }}
                    sx={{ width: '15%', mr: '2%' }}
                />
                <Typography variant='h5' display='inline' sx={{ mt: 2 }}>A</Typography>
                <TextField
                    id="input-time-end"
                    label="Hasta (H)"
                    variant="standard"
                    type="number"
                    value={responseQ.timeEnd}
                    onChange={handleInputTimeEnd}
                    required
                    inputProps={{
                        inputMode: 'numeric',
                        pattern: '[0-9]*',
                        min: 0,
                        max: 24
                    }}
                    sx={{ width: '15%', ml: '2%' }}
                />
            </Box>
            <Box sx={{ mt: '10vh' }} textAlign='center'>
                <Button sx={{ mr: '10%' }} variant="outlined" onClick={handleButtonBack}>Atr&aacute;s</Button>
                <Button sx={{ ml: '10%' }} variant="outlined" disabled={error} type="submit">Siguente</Button>
            </Box>
        </Box>
    );
}

