function ExtraPlace({ namePlaces, onNamePlaces }) {
    const [optCheck, setOptCheck] = React.useState([
        { chekc: false, value: 'tienda' },
        { chekc: false, value: 'centroc' },
        { chekc: false, value: 'oficina' },
        { chekc: false, value: 'stand' },
        { chekc: false, value: 'hosteleria' },
        { chekc: false, value: 'ventas' },
        { chekc: false, value: 'otros' }
    ]);

    const handleNamePlaces = (e) => {
        const valueBox = e.target.value;
        const checkBox = e.target.checked;

        let toNamePlaces = [];
        const toOptCh = optCheck.map((opt) => {
            if (opt.value === valueBox) {
                if (checkBox) {
                    toNamePlaces.push(valueBox);
                }
                return { chekc: checkBox, value: valueBox };
            } else {
                if (opt.chekc) {
                    toNamePlaces.push(opt.value);
                }
                return opt;
            }
        });
        setOptCheck(toOptCh);
        onNamePlaces(toNamePlaces);
    };

    return (
        <Box sx={{ mt: '2vh' }}>
            <Typography align='center' variant='body1'>Com&eacute;ntenos si fue en:</Typography>
            <Box
                display="flex"
                alignItems="center"
                justifyContent="center"
                sx={{ mt: '2vh' }}
            >
                <FormGroup>
                    <FormControlLabel control={<Checkbox checked={optCheck[0].chekc} value={optCheck[0].value} onChange={handleNamePlaces} />} label="TIENDA" />
                    <FormControlLabel control={<Checkbox checked={optCheck[1].chekc} value={optCheck[1].value} onChange={handleNamePlaces} />} label="CENTRO COMERCIAL" />
                    <FormControlLabel control={<Checkbox checked={optCheck[2].chekc} value={optCheck[2].value} onChange={handleNamePlaces} />} label="OFICINA" />
                    <FormControlLabel control={<Checkbox checked={optCheck[3].chekc} value={optCheck[3].value} onChange={handleNamePlaces} />} label="STAND" />
                    <FormControlLabel control={<Checkbox checked={optCheck[4].chekc} value={optCheck[4].value} onChange={handleNamePlaces} />} label="HOSTELER&Iacute;A" />
                    <FormControlLabel control={<Checkbox checked={optCheck[5].chekc} value={optCheck[5].value} onChange={handleNamePlaces} />} label="VENTAS" />
                    <FormControlLabel control={<Checkbox checked={optCheck[6].chekc} value={optCheck[6].value} onChange={handleNamePlaces} />} label="OTROS" />
                </FormGroup>
            </Box>
        </Box>
    );
}

function QuestionDetallesPersonal({ CuestData, onCuestDataChange }) {
    const [noobCandidate, setNoobCandidate] = React.useState('no');
    const [extraPlaces, setExtraPlaces] = React.useState(false);
    const [namePlaces, setNamePlaces] = React.useState([]);
    const [nameSocialLinks, setNameSocialLinks] = React.useState([
        { chekc: false, value: 'facebook' },
        { chekc: false, value: 'instagram' },
        { chekc: false, value: 'twitter' },
        { chekc: false, value: 'linkedin' },
        { chekc: false, value: 'youtube' },
        { chekc: false, value: 'otros' }
    ]);
    const [socialLinks, setSocialLinks] = React.useState([]);

    const handleRadioNoob = (e) => {
        const valueNoob = e.target.value;
        if (valueNoob === 'si') {
            setExtraPlaces(<ExtraPlace NamePlaces={namePlaces} onNamePlaces={setNamePlaces} />);
        } else {
            setExtraPlaces(false);
        }
        setNoobCandidate(valueNoob);
    };

    const handleNameSocialLinks = (e) => {
        const valueBox = e.target.value;
        const checkBox = e.target.checked;

        let toNamePlaces = [];
        const toOptCh = nameSocialLinks.map((opt) => {
            if (opt.value === valueBox) {
                if (checkBox) {
                    toNamePlaces.push(valueBox);
                }
                return { chekc: checkBox, value: valueBox };
            } else {
                if (opt.chekc) {
                    toNamePlaces.push(opt.value);
                }
                return opt;
            }
        });
        setNameSocialLinks(toOptCh);
        setSocialLinks(toNamePlaces);
    };

    const handleAswer = (resp) => {
        onCuestDataChange({
            ...CuestData,
            lineProgress: 90,
            personalDetails: {
                expClientHelp: noobCandidate === 'si',
                expClientHelpIn: namePlaces,
                socialLinks: socialLinks
            }
        });
    };

    const handleButtonBack = (e) => {
        onCuestDataChange({
            ...CuestData,
            lineProgress: 50
        });
    };

    return (
        <Box>
            <Typography
                variant='h5'
                align='center'
                sx={{ mt: '10vh' }}
            >&#191;Has trabajado alguna vez en atenci&oacute;n al cliente?</Typography>
            <Box
                display="flex"
                alignItems="center"
                justifyContent="center"
                sx={{ mt: '2vh' }}
            >
                <RadioGroup
                    aria-label="noob"
                    name="radio-noob"
                    value={noobCandidate}
                    onChange={handleRadioNoob}
                    row
                >
                    <FormControlLabel value='si' control={<Radio />} label="Si" />
                    <FormControlLabel value='no' control={<Radio />} label="No" />
                </RadioGroup>
            </Box>
            {extraPlaces}
            <Box sx={{ mt: '7vh' }}>
                <Typography align='center' variant='body1'>&#191;Qu&eacute; redes sociales conoces?</Typography>
                <Box
                    display="flex"
                    alignItems="center"
                    justifyContent="center"
                    sx={{ mt: '2vh' }}
                >
                    <FormGroup>
                        <FormControlLabel control={<Checkbox checked={nameSocialLinks[0].chekc} value={nameSocialLinks[0].value} onChange={handleNameSocialLinks} />} label="FACEBOOK" />
                        <FormControlLabel control={<Checkbox checked={nameSocialLinks[1].chekc} value={nameSocialLinks[1].value} onChange={handleNameSocialLinks} />} label="INSTAGRAM" />
                        <FormControlLabel control={<Checkbox checked={nameSocialLinks[2].chekc} value={nameSocialLinks[2].value} onChange={handleNameSocialLinks} />} label="TWITTER" />
                        <FormControlLabel control={<Checkbox checked={nameSocialLinks[3].chekc} value={nameSocialLinks[3].value} onChange={handleNameSocialLinks} />} label="LINKEDIN" />
                        <FormControlLabel control={<Checkbox checked={nameSocialLinks[4].chekc} value={nameSocialLinks[4].value} onChange={handleNameSocialLinks} />} label="YOUTUBE" />
                        <FormControlLabel control={<Checkbox checked={nameSocialLinks[5].chekc} value={nameSocialLinks[5].value} onChange={handleNameSocialLinks} />} label="OTROS" />
                    </FormGroup>
                </Box>
            </Box>
            <Box textAlign='center' sx={{ my: '10vh' }}>
                <Button sx={{ mr: '10%' }} onClick={handleButtonBack} variant="outlined">Atr&aacute;s</Button>
                <Button sx={{ ml: '10%' }} onClick={handleAswer} variant="outlined">Siguente</Button>
            </Box>
        </Box>
    );
}

function QuestionSueldo({ CuestData, onCuestDataChange }) {
    const [empleo, setEmpleo] = React.useState('');
    const [rangoSalario, setRangoSalario] = React.useState('rang-1');

    const handleSubmit = (e) => {
        onCuestDataChange({
            ...CuestData,
            lineProgress: 100,
            economicDetails: {
                work: empleo,
                salarial: rangoSalario
            }
        });
    };

    const handleEmpleo = (e) => {
        setEmpleo(e.target.value);
    };

    const handleRangoSalario = (e) => {
        setRangoSalario(e.target.value);
    };

    const handleButtonBack = (e) => {
        onCuestDataChange({
            ...CuestData,
            lineProgress: 80
        });
    };

    return (
        <Box
            component='form'
            onSubmit={handleSubmit}
        >
            <Typography
                variant='h5'
                align='center'
                sx={{ mt: '10vh' }}
            >Si tuvieses que elegir un trabajo de por vida. &#191;En qu&eacute; te gstar&iacute;a trabajar?</Typography>
            <TextField
                id="input-empleo"
                label='Empleo'
                variant="standard"
                required
                fullWidth
                onChange={handleEmpleo}
                value={empleo}
                sx={{ mt: '2vh' }}
            />
            <Box sx={{ mt: '7vh' }}>
                <Typography variant='body1' align='center'>&#191;Qu&eacute; sueldo te gustar&iacute;a ganar al mes?</Typography>
                <Box
                    display="flex"
                    alignItems="center"
                    justifyContent="center"
                    sx={{ mt: '2vh' }}
                >
                    <RadioGroup
                        aria-label="noob"
                        name="radio-noob"
                        defaultValue='rang-1'
                        value={rangoSalario}
                        onChange={handleRangoSalario}
                    >
                        <FormControlLabel value='rang-1' control={<Radio defaultChecked />} label="De 400&euro; a 1000&euro;" />
                        <FormControlLabel value='rang-2' control={<Radio />} label="De 1000&euro; a 1500&euro;" />
                        <FormControlLabel value='rang-3' control={<Radio />} label="De 1500&euro; a 2500&euro;" />
                        <FormControlLabel value='rang-4' control={<Radio />} label="De 2500&euro; a 5000&euro;" />
                        <FormControlLabel value='rang-5' control={<Radio />} label="+5000&euro;" />
                    </RadioGroup>
                </Box>
            </Box>
            <Box textAlign='center' sx={{ my: '10vh' }}>
                <Button sx={{ mr: '10%' }} variant="outlined" onClick={handleButtonBack}>Atr&aacute;s</Button>
                <Button sx={{ ml: '10%' }} type='submit' variant="outlined">Siguente</Button>
            </Box>
        </Box>
    );
}

function QuestionEnd({ CuestData, onCuestDataChange }) {
    const handleButtonBack = (e) => {
        onCuestDataChange({
            ...CuestData,
            lineProgress: 90
        });
    };

    const handleCompleteQuest = (e) => {
        alert(JSON.stringify(CuestData));
    };

    return (
        <Box>
            <Typography
                variant='h5'
                align='center'
                sx={{ mt: '10vh' }}
            >
                Hemos conclu&iacute;do con el cuestionario. Si est&aacute; listo, proceda a agendar una cita para la entrevista presencial en nuestras oficinas.
            </Typography>
            <Box sx={{ my: '10vh' }} textAlign='center'>
                <Button sx={{ mr: '10%' }} variant="outlined" onClick={handleButtonBack}>Atr&aacute;s</Button>
                <Button sx={{ ml: '10%' }} variant="outlined" onClick={handleCompleteQuest}>Agendar Cita</Button>
            </Box>
        </Box>
    );
}

const cuestForm = {
    lineProgress: 0,
    disponibleIdeal: '',
    likeClientHelp: '',
    timeWork: { situation: '', timeStart: '', timeEnd: '' },
    conditionsWork: '',
    personalDetails: { expClientHelp: '', expClientHelpIn: '', socialLinks: '' },
    economicDetails: { work: '', salarial: '' }
};

function ErrorStep({ CuestData, onCuestDataChange }) {
    const handleButtonBack = (e) => {
        onCuestDataChange(cuestForm);
    };

    return (
        <Box>
            <Typography
                variant='h5'
                align='center'
            >
                Ups!!! Ha ocurrido un error.
            </Typography>
            <Box sx={{ mt: '5vh' }} textAlign='center'>
                <Button sx={{ mr: '10%' }} variant="outlined" onClick={handleButtonBack}>Regresar</Button>
            </Box>
        </Box>
    );
}

export default function Cuestionario({ CompleteStep, onFinishStep }) {
    const [cuestData, setCuestData] = React.useState(cuestForm);

    const showQuest = () => {
        switch (cuestData.lineProgress) {
            case 0:
                return (
                    <QuestionDisponibleLaboral
                        CuestData={cuestData}
                        onCuestDataChange={setCuestData}
                    />);

            case 20:
                return (
                    <QuestionAtencionCliente
                        CuestData={cuestData}
                        onCuestDataChange={setCuestData}
                    />);

            case 30:
                return (
                    <QuestionTiempo
                        CuestData={cuestData}
                        onCuestDataChange={setCuestData}
                    />);

            case 50:
                return (
                    <QuestionCondiciones
                        CuestData={cuestData}
                        onCuestDataChange={setCuestData}
                    />);

            case 80:
                return (
                    <QuestionDetallesPersonal
                        CuestData={cuestData}
                        onCuestDataChange={setCuestData}
                    />);

            case 90:
                return (
                    <QuestionSueldo
                        CuestData={cuestData}
                        onCuestDataChange={setCuestData}
                    />);

            case 100:
                const handleButtonBack = (e) => {
                    setCuestData({
                        ...cuestData,
                        lineProgress: 90
                    });
                };
            
                const handleCompleteQuest = (e) => {
                    alert(JSON.stringify(cuestData));
                    onFinishStep({...CompleteStep, cuestionario: true});
                };

                return (
                    <Box>
                        <Typography
                            variant='h5'
                            align='center'
                            sx={{ mt: '10vh' }}
                        >
                            Hemos conclu&iacute;do con el cuestionario. Si est&aacute; listo, proceda a agendar una cita para la entrevista presencial en nuestras oficinas.
                        </Typography>
                        <Box sx={{ my: '10vh' }} textAlign='center'>
                            <Button sx={{ mr: '10%' }} variant="outlined" onClick={handleButtonBack}>Atr&aacute;s</Button>
                            <Button sx={{ ml: '10%' }} variant="outlined" onClick={handleCompleteQuest}>Agendar Cita</Button>
                        </Box>
                    </Box>
                );

            default:
                return <ErrorStep
                    CuestData={cuestData}
                    onCuestDataChange={setCuestData}
                />;
        }
    };

    return (
        <Box
            sx={{
                width: { sm: '90%', md: '60%' },
                height: '100%',
                px: '10%',
                pt: '5%',
                pb: '5%',
                my: { sm: 0, md: '5vh' },
                mx: { sm: 0, md: '10%' },
                boxShadow: { sm: 0, md: 1 },
            }}
        >
            <Box sx={{ width: '100%' }}>
                <LinearProgress variant="determinate" value={cuestData.lineProgress} />
                <Box sx={{ pt: 5 }} />
                {showQuest()}
            </Box>
        </Box>
    );
}